*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  -webkit-transition: 0.1s ease-in;
  -moz-transition: 0.1s ease-in;
  -o-transition: 0.1s ease-in;
  transition: 0.1s ease-in;
  background-color: var(--very-dark-blue-background);
  color: var(--white-background);
}

h1,
h2 {
  -webkit-transition: 0.1s ease-in;
  -moz-transition: 0.1s ease-in;
  -o-transition: 0.1s ease-in;
  transition: 0.1s ease-in;
}

h1 {
  margin: 0 0 1.4rem 0;
}

header {
  color: var(--white-text);
}

img {
  max-width: 100%;
}

fieldset {
  padding: 0;
  border: none;
}

body {
  font-family: "Inter", sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: clamp(2.4rem, 3vw, 2.6rem);
  white-space: nowrap;
}

h2 {
  font-size: 2.4rem;
  text-transform: capitalize;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.header {
  font-size: 1.4rem;
  font-weight: 600;
  letter-spacing: 0.2px;
}

.card--top__top__username {
  font-size: 1.2rem;
  font-weight: 700;
}
.card--top__middle__count {
  font-size: 5.2rem;
  font-weight: 700;
}
.card--top__middle__followers {
  font-size: 1.3rem;
  font-weight: 200;
  text-transform: uppercase;
  letter-spacing: 4px;
}
.card--overview__top {
  font-size: 1.4rem;
  font-weight: 600;
  text-transform: capitalize;
}
.card--overview__bottom {
  font-size: 3rem;
  font-weight: 700;
}
.card__rating {
  font-size: 1.3rem;
  font-weight: 600;
}

.card {
  display: flex;
  max-width: 32.6rem;
  background-color: var(--dark-desaturated-blue-card);
  border-radius: 6px;
}
.card--top {
  position: relative;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 0 0 2.5rem 0;
  padding: 3rem 7rem 2.4rem;
}
.card--top__top {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.card--top__top__username {
  color: var(--desaturated-blue-card);
}
.card--top__top p {
  margin: 0 0 0 1rem;
}
.card--top__middle {
  padding: 2rem 0 2.8rem 0;
}
.card--top__middle__count {
  color: var(--white-text);
}
.card--top__middle__followers {
  margin: 0.4rem 0 0 0;
  color: var(--desaturated-blue-card);
}
.card--top__facebook:before {
  content: "";
  display: block;
  height: 5px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--facebook);
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  transition: all 0.2s ease-in-out;
}
.card--top__facebook:hover:before {
  width: 0;
}
.card--top__twitter:before {
  content: "";
  display: block;
  height: 5px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--twitter);
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  transition: all 0.2s ease-in-out;
}
.card--top__twitter:hover:before {
  width: 0;
}
.card--top__instagram:before {
  content: "";
  display: block;
  height: 5px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--instagram-start);
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  background: linear-gradient(225deg, var(--instagram-start), var(--instagram-middle), var(--instagram-end));
  transition: all 0.2s ease-in-out;
}
.card--top__instagram:hover:before {
  width: 0;
}
.card--top__youtube:before {
  content: "";
  display: block;
  height: 5px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--youtube);
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  transition: all 0.2s ease-in-out;
}
.card--top__youtube:hover:before {
  width: 0;
}
@media only screen and (min-width: 23.125em) {
  .card--top {
    padding: 3.2rem 10rem 2.4rem;
  }
}
@media only screen and (min-width: 48em) {
  .card--top {
    min-width: 326px;
  }
  .card--top:nth-child(even) {
    margin: 0 0 2rem auto;
  }
  .card--top:nth-child(odd) {
    margin: 0 auto 2rem 0;
  }
}
@media only screen and (min-width: 62em) {
  .card--top {
    min-width: 200px;
    padding: 3.2rem 0 2.4rem 0;
  }
  .card--top:nth-child(even) {
    margin: 0;
  }
  .card--top:nth-child(odd) {
    margin: 0;
  }
}
@media only screen and (min-width: 75em) {
  .card--top {
    padding: 3.2rem 4rem 2.4rem;
  }
}
@media only screen and (min-width: 87.5em) {
  .card--top {
    min-width: 254px;
  }
}
.card--overview {
  flex-direction: column;
  align-items: space-between;
  padding: 2.7rem 3.1rem 2.7rem 2.6rem;
  margin: 0 0 1.8rem 0;
  min-width: 276px;
}
.card--overview__top, .card--overview__bottom {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.card--overview__top {
  margin: 0 0 1.4rem 0;
}
.card--overview__top p {
  color: var(--desaturated-blue-card);
}
.card--overview__bottom__number {
  color: var(--white-text);
}
@media only screen and (min-width: 23.125em) {
  .card--overview {
    min-width: 327px;
  }
}
@media only screen and (min-width: 48em) {
  .card--overview {
    min-width: 326px;
  }
}
@media only screen and (min-width: 62em) {
  .card--overview {
    min-width: 200px;
    margin: 0;
  }
}
@media only screen and (min-width: 87.5em) {
  .card--overview {
    min-width: 254px;
  }
}
.card:hover {
  cursor: pointer;
  box-shadow: 0 10px 10px rgba(204, 207, 219, 0.05);
  background-color: var(--hover-background);
}
.card__ascent {
  color: var(--arrow-up);
}
.card__descent {
  color: var(--arrow-down);
}
.card__ascent::before {
  content: "▲";
  color: var(--arrow-up);
  margin: 3px;
}
.card__descent::before {
  content: "▼";
  color: var(--arrow-down);
  margin: 3px;
}

.toggle__wrapper {
  font-size: var(--baseSize);
  display: inline-block;
  vertical-align: middle;
  width: 4.8rem;
  height: 2.4rem;
  border-radius: 3.5em;
  /* = height */
  position: relative;
}
.toggle__wrapper input[type=radio] {
  display: inline-block;
  margin-right: -2px;
  width: 50%;
  height: 100%;
  opacity: 0;
  position: relative;
  z-index: 1;
  cursor: pointer;
}
.toggle__wrapper input[type=radio]:focus ~ .c-toggle__switcher {
  border: 3px solid #a1456e;
  top: -2px;
}
.toggle__background {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  transition: all 0.1s ease-out;
  background: var(--toggle-dark);
  border-radius: 3.5em;
}
.toggle__button {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 100%;
  z-index: 2;
  width: 40%;
  height: 80%;
  margin: 0.25rem 0 0 0.2rem;
  transition: all 0.1s ease-out;
  background: var(--dark-desaturated-blue-card);
  border-radius: 50%;
}

#dark:checked ~ .toggle__button {
  right: 0;
  left: 50%;
  background: var(--white);
}

#dark:checked ~ .toggle__background {
  background: var(--toggle-light);
}

.background {
  -webkit-transition: 0.1s ease-in;
  -moz-transition: 0.1s ease-in;
  -o-transition: 0.1s ease-in;
  transition: 0.1s ease-in;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: -9;
  height: 233px;
  background-color: var(--very-dark-blue-background-pattern);
  border-bottom-left-radius: 20px;
  border-bottom-left-radius: 20px;
}

.cards {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
}
.cards__top {
  margin: 0 auto;
}
@media only screen and (min-width: 48em) {
  .cards__top {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-content: space-around;
    margin: 0;
  }
}
@media only screen and (min-width: 62em) {
  .cards__top {
    display: flex;
    justify-content: space-between;
  }
}
.cards__overview {
  margin: 3.4rem auto 0;
}
.cards__overview__header {
  margin: 0 0 3.5rem 0;
}
@media only screen and (min-width: 62em) {
  .cards__overview__header {
    margin: 4.6rem 0 3rem 0;
  }
}
@media only screen and (min-width: 48em) {
  .cards__overview__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}
@media only screen and (min-width: 62em) {
  .cards__overview__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 3.3rem;
    margin: 0;
  }
}
@media only screen and (min-width: 75em) {
  .cards__overview__wrapper {
    gap: 2.3rem;
  }
}
@media only screen and (min-width: 87.5em) {
  .cards__overview__wrapper {
    gap: 3rem;
  }
}
@media only screen and (min-width: 62em) {
  .cards__overview {
    margin: 0;
  }
}

.container {
  width: 100%;
  padding-right: 2.4rem;
  padding-left: 2.4rem;
  margin-right: auto;
  margin-left: auto;
  max-width: 540px;
  margin: 3.8rem auto 4.6rem auto;
}
@media only screen and (min-width: 48em) {
  .container {
    max-width: 720px;
  }
}
@media only screen and (min-width: 62em) {
  .container {
    width: 100%;
    padding-right: 3rem;
    padding-left: 3rem;
    margin-right: auto;
    margin-left: auto;
    max-width: 960px;
  }
}
@media only screen and (min-width: 75em) {
  .container {
    width: 100%;
    padding-right: 10.5rem;
    padding-left: 10.5rem;
    margin-right: auto;
    margin-left: auto;
    max-width: 1110px;
  }
}
@media only screen and (min-width: 87.5em) {
  .container {
    max-width: 1320px;
  }
}

.header {
  display: block;
  max-width: 320px;
  margin: 0 auto 2.6rem auto;
}
.header__text {
  padding: 0 0 2.4rem 0;
  color: var(--desaturated-blue-card);
  border-bottom: 1px solid #3e4051;
}
@media only screen and (min-width: 62em) {
  .header__text {
    border-bottom: none;
  }
}
.header__toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 0 0 0;
}
.header__toggle label {
  color: var(--desaturated-blue-card);
  cursor: pointer;
  margin: 0 1.3rem;
}
@media only screen and (min-width: 62em) {
  .header__toggle {
    padding: 1.6rem 0 4.8em 0;
  }
}
@media only screen and (min-width: 48em) {
  .header {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 720px;
    margin: 0;
  }
}
@media only screen and (min-width: 62em) {
  .header {
    max-width: 960px;
  }
}
@media only screen and (min-width: 75em) {
  .header {
    max-width: 1110px;
  }
}
@media only screen and (min-width: 87.5em) {
  .header {
    max-width: 1320px;
  }
}